body {
  width: 100vw;
  height: 100vh;
  background: #45B3E9;
}


@font-face {
  font-family: 'TT Commons';
  src: url('../../public/fonts/secondFonts/TTCommons-Regular.eot');
  src: local('TT Commons Regular'), local('TTCommons-Regular'),
  url('../../public/fonts/secondFonts/TTCommons-Regular.eot?#iefix') format('embedded-opentype'),
  url('../../public/fonts/secondFonts/TTCommons-Regular.woff2') format('woff2'),
  url('../../public/fonts/secondFonts/TTCommons-Regular.woff') format('woff'),
  url('../../public/fonts/secondFonts/TTCommons-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}








@font-face {
  font-family: "QIWIPixel-Regular";
  src: url("../../public/fonts/QIWIPixel-Regular.otf") format("truetype");
