.HomePage {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #45B3E9;
  //padding: 44px 80px;
  height: 100vh;
  justify-content: space-between;

  .cloud{
    position: absolute;
    &.one{
      top: 21px;
      width: 95px;
      height: 48px;
      right: -20px;
    }
    &.two{
      top: 166px;
      left: -20px;
      width: 94.96px;
      height: 47.48px;
    }
    &.three{
      width: 59.42px;
      top: 285px;
      height: 29.71px;
      right: 0;
    }
    &.four{
      left: 28px;
      width: 46.58px;
      height: 23.29px;
      top: 371px;
    }
  }
  img{
    width: 70%;
  }
  .boxFlex {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  p {
    max-width: 225px;
    font-family: "TT Commons";
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 28px;
    text-align: center;

    color: #FFFFFF;
  }

  .textMainApp {
    font-weight: 400;
    font-size: 62px;
    line-height: 74px;
    font-family: "QIWIPixel-Regular";
    color: white;
    -webkit-text-stroke: 1px black;
    text-shadow: 2px 2px 0 #000,
    -1px -1px 0 #000,
    1px -1px 0 #000,
    -1px 1px 0 #000,
    1px 1px 0 #000;
  }

  button {
    margin-bottom: 50px;
    background: #8044C7;
    border: 1.24971px solid #000000;
    border-radius: 17.496px;
    font-family: 'TT Commons';
    font-style: normal;
    font-weight: 500;
    font-size: 32px;
    line-height: 37px;
    display: flex;
    align-items: center;
    text-align: center;
    padding: 15px 93px 15px 93px;
    color: #FFFFFF;

  }

}
.Plug{
  display: flex;
  img{
    position: absolute;
    bottom: 0;
  }
  .textMainApp {
    font-weight: 400;
    font-size: 62px;
    line-height: 74px;
    font-family: "QIWIPixel-Regular";
    color: white;
    -webkit-text-stroke: 1px black;
    text-shadow: 2px 2px 0 #000,
    -1px -1px 0 #000,
    1px -1px 0 #000,
    -1px 1px 0 #000,
    1px 1px 0 #000;
  }
  .blockText{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    .title{
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 74px;
    }
  }
  p {
    font-family: "TT Commons";
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 28px;
    text-align: center;

    color: #FFFFFF;
  }
  .qr{
    position: relative;z-index: 200;
  }
}


.endPage {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #45B3E9;
  padding: 0 20px;
  height: 100vh;
  justify-content: space-between;
  .cloud{
    position: absolute;
    &.one{
      top: 27.42px;
      left: -10px;
      width: 63.65px;
      height: 31.82px;
    }
    &.two{
      top: 20.79px;
      right: 48px;
      width: 47.74px;
      height: 23.87px;
    }
    &.three{
      top: 108px;
      right: -10px;
    }
  }
  .boxFlex {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  p {
    font-family: "TT Commons";
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 28px;
    text-align: center;

    color: #FFFFFF;
  }

  .textMainApp {
    font-weight: 400;
    font-size: 62px;
    line-height: 74px;
    font-family: "QIWIPixel-Regular";
    color: white;
    -webkit-text-stroke: 1px black;
    text-shadow: 2px 2px 0 #000,
    -1px -1px 0 #000,
    1px -1px 0 #000,
    -1px 1px 0 #000,
    1px 1px 0 #000;
  }

  button {
    margin-bottom: 50px;
    font-family: 'TT Commons';
    font-style: normal;
    font-weight: 500;
    font-size: 32px;
    line-height: 37px;
    display: flex;
    align-items: center;
    text-align: center;
    padding: 15px 93px 15px 93px;
    color: #FFFFFF;
    background: #FF8000;
    border: 1.24971px solid #FFFFFF;
    border-radius: 17.496px;
  }
  .count {

    display: flex;
    width: 100%;
    justify-content: space-around;
    .contNumber {
      display: flex;
      flex-direction: column;
      span {
        font-family: "TT Commons";
        font-style: normal;
        font-weight: 400;
        font-size: 64px;
        line-height: 74px;
        text-align: center;
        color: #FFFFFF;
      }
    }
  }
}
.score{
  font-weight: 400;
  font-size: 62px;
  font-family: "QIWIPixel-Regular";
  color: white;
  -webkit-text-stroke: 1px black;
  text-shadow: 2px 2px 0 #000,
  -1px -1px 0 #000,
  1px -1px 0 #000,
  -1px 1px 0 #000,
  1px 1px 0 #000;
  position: absolute;
  left: 20px;
  top: 0;
  z-index: 1000;
  font-style: normal;
  line-height: 115px;
}
